import { OnInit, Input, DoCheck } from '@angular/core';
import { AbstractControl } from '@angular/forms';

export abstract class AbstractReactiveComponent implements OnInit, DoCheck {
  @Input() label: string | null = 'input';

  @Input() fieldId: string | null = null;

  @Input() placeholder: string | null = 'write some thing';

  @Input() required: boolean = false;

  @Input() rtldirection: boolean = false;

  @Input() tooltipMsg: string | null = null;

  @Input() control: AbstractControl | null = null;

  @Input() groupClass;

  @Input() fieldClass;

  validationErrors: object = null;

  ngOnInit() {}

  ngDoCheck() {
    if (this.control) {
      this.validationErrors =
        this.control.touched && this.control.invalid
          ? this.control.errors
          : null;
    }
  }
}
