import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
} from '@angular/core';
import { InputSelectItem } from 'src/app/models/InputSelectItem';
import { AbstractReactiveComponent } from '../abstract-component';

import { NgSelectComponent } from '@ng-select/ng-select';

@Component({
  selector: 'app-multi-select',
  templateUrl: './multi-select.component.html',
  styleUrls: ['./multi-select.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiSelectComponent
  extends AbstractReactiveComponent
  implements OnInit, OnChanges {
  constructor() {
    super();
  }

  @Input() ItemsList: InputSelectItem[] = null;
  @Input() initialSelectedList: InputSelectItem[];

  @Output() itemselected = new EventEmitter<any>();

  selectedItemsIds: string[];

  ngOnInit() {}
  ngOnChanges() {
    if (this.initialSelectedList) {
      if (this.initialSelectedList.length > 0) {
        console.log(this.initialSelectedList);
        this.selectedItemsIds = this.initialSelectedList.map((e) => e._id);
      }
    }
  }

  ItemSelected(event) {
    let selectedList = [];
    for (let index = 0; index < event.length; index++) {
      selectedList.push(this.ItemsList.find((x) => x._id === event[index]));
    }

    this.itemselected.emit(selectedList);
  }

  clear(event) {
    // this.selectedItem = null;
    // this.itemselected.emit([]);
  }
}
