import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
} from '@angular/core';
import { InputSelectItem } from 'src/app/models/InputSelectItem';
import { AbstractReactiveComponent } from '../abstract-component';

@Component({
  selector: 'app-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class SelectComponent
  extends AbstractReactiveComponent
  implements OnInit, OnChanges {
  constructor() {
    super();
  }

  @Input() ItemsList: InputSelectItem[] = null;
  @Input() initialSelectedItem: InputSelectItem;

  @Output() itemselected = new EventEmitter<any>();

  selectedItemsId: string;

  ngOnInit() {}
  ngOnChanges() {
    if (this.initialSelectedItem) {
      this.selectedItemsId = this.initialSelectedItem._id;
    }
  }

  selectedItem: InputSelectItem;
  ItemSelected(event) {
    this.selectedItem = this.ItemsList.find((x) => x._id === event);

    this.itemselected.emit(this.selectedItem);
  }

  clear(event) {
    this.selectedItem = null;
    this.itemselected.emit(event);
  }
}
