import {
  Component,
  Input,
  TemplateRef,
  ChangeDetectionStrategy,
  Output,
  EventEmitter,
} from "@angular/core";
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { AbstractReactiveComponent } from "../abstract-component";

@Component({
  selector: "app-upload-photo",
  templateUrl: "./upload-photo.component.html",
  styleUrls: ["./upload-photo.component.scss"],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class UploadPhotoComponent extends AbstractReactiveComponent {
  @Input() labelImg;

  @Input() image: string;

  @Output() choosed_image = new EventEmitter<string>();

  constructor(private modalService: BsModalService) {
    super();
  }

  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>) {
    if (!this.image) {
      this.image = "../../../../../assets/placeholder.png";
    }
    this.modalRef = this.modalService.show(template);
    document.getElementById(
      "imageModal"
    ).style.backgroundImage = `url(${this.image})`;
  }

  detectFile(event) {
    const { files } = event.target;
    if (files && files[0]) {
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = (e: any) => {
        this.image = e.target.result;
        this.choosed_image.emit(this.image);
      };
      this.control.patchValue(files[0]);
    }
  }

  deletePhoto() {
    this.image = undefined;
    this.control.patchValue(undefined);
  }
}
