import {
  Component,
  Input,
  TemplateRef,
  ChangeDetectionStrategy,
  Output,
  OnChanges,
  EventEmitter,
} from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { AbstractReactiveComponent } from '../abstract-component';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';

@Component({
  selector: 'app-upload-multi-photos',
  templateUrl: './upload-multi-photos.component.html',
  styleUrls: ['./upload-multi-photos.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class UploadMultiPhotosComponent
  extends AbstractReactiveComponent
  implements OnChanges {
  images: string[] = [];
  @Input() initialPhotos: string[];

  @Output() choosed_image = new EventEmitter<string>();

  constructor(private modalService: BsModalService, private fb: FormBuilder) {
    super();
  }

  ngOnChanges() {
    if (this.initialPhotos) {
      this.images = this.initialPhotos;
      this.addPhoto();
      for (let index = 0; index < this.images.length; index++) {
        this.addPhoto();
      }
    } else {
      this.addPhoto();
    }
  }

  // form = this.fb.group({
  //   photos: this.fb.array([]),
  // });

  //Help to get all photos controls as form array.
  get photos(): FormArray {
    return this.control as FormArray;
  }

  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>, i) {
    if (!this.images[i]) {
      this.images[i] = '../../../../../assets/placeholder.png';
    }
    this.modalRef = this.modalService.show(template);
    document.getElementById(
      'imageModal'
    ).style.backgroundImage = `url(${this.images[i]})`;
  }

  addPhoto() {
    this.photos.push(this.fb.control({ image: [] }));
  }

  detectFile(event, i) {
    const { files } = event.target;
    if (files && files[0]) {
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = (e: any) => {
        this.images[i] = e.target.result;
        this.choosed_image.emit(this.images[i]);
      };
      this.photos.controls[i].patchValue(files[0]);
    }
  }

  deletePhoto(i) {
    this.images.splice(i, 1);
    this.photos.removeAt(i);
  }
}
