import { Component, Input, OnChanges } from '@angular/core';
import { FILLING_NEEDED_MESSAGE } from './validation-messages-list';
import { PATTERNS_LIST } from './validation-patterns-list';

@Component({
  selector: 'app-validation-errors',
  templateUrl: './validation-errors.component.html',
  styleUrls: ['./validation-errors.component.scss'],
})
export class ValidationErrorsComponent implements OnChanges {

  @Input() validationErrors: object | null = null;
  errorMessage: string | null = null;

  ngOnChanges(changes): void {
    this.errorMessage = this.getErrorMessage();
  }

  getErrorMessage(): string | null {
    const errors = this.validationErrors;
    if (errors) {
      // console.log(errors);
      const keys   = Object.keys(errors);
      switch (keys[0]) {
        case 'required'  : return FILLING_NEEDED_MESSAGE;
        case 'pattern'   : return this.getPatternMessage(errors['pattern']['requiredPattern']);
        case 'minlength' : return 'Minimum Lengths:' + errors['minlength'].requiredLength;
        case 'min': return FILLING_NEEDED_MESSAGE;
        default:
          return 'UnKnowen Error';
      }
    }
    return null;
  }

  getPatternMessage(requiredPattern: string): string {
    return PATTERNS_LIST.filter(x => x['PATTERN'] === requiredPattern)[0]['MESSAGE'];
  }
}
